@media (prefers-reduced-motion: no-preference) {}
@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mukta:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display@1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap');

body {
  background-color: rgba(255, 238, 204, 0.6);
  font-size: 2vh;
  margin: 0;
  font-family: 'Patrick Hand', cursive;
  height: 100%;
  margin: auto;
}

.App {
  height: 100%;
}

footer {
  position: fixed;
  bottom: 2%;
  width: 100%;
  max-width: 400px;
  z-index: 3;
}

.App-navtab {
  z-index: 3;
  position: fixed;
  width: 100%;
  max-width: 400px;
}

#root {
  height: 100%;
  max-width: 400px;
  margin: auto;
  background-repeat: repeat-y;
}

.App-content {
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
  padding-top: 30%;
  padding-bottom: 20%;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes App-background-image {
  from {
    background: -webkit-linear-gradient(120deg, #FFEECC 50%, #FFBBCC 50%);
    transition: 1s ease-out;
  }

  to {
    background: -webkit-linear-gradient(120deg, #FFEECC 50%, #FFEECC 50%);
    transition: 1s ease-out;
  }
}
