  .InpSelect {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
 
  .InpSelect.disabled {
    pointer-events: none;
    opacity: 80%;
  }


  /* Hide the browser's default checkbox */
  .InpSelect input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0; 
    width: 0;
  }
  
  /* Create a custom checkbox */
   .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    border-radius: 5px;
    background-color: #eee;
  }
  
  /* On mouse-over, add a grey background color */
  .InpSelect:hover input ~ .checkmark {
    background-color: #ccc;
  }
  
  /* When the checkbox is checked, add a blue background */
  .InpSelect input:checked ~ .checkmark {
    background-color: #bc318f;
  }
  
  /* Create the checkmark/indicator (hidden when not checked) */
  .InpSelect .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the checkmark when checked */
  .InpSelect input:checked ~ .checkmark:after {
    display: block;
  }
  
  /* Style the checkmark/indicator */
  .InpSelect .checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
